
/**
 * @author schwada
 * @since 10.1.2019
 * 
 * A wrapper class around the user identity data
 * available for different components in the app.
 */

export class Identity {
    
    private birthdate;
    private location: string;
    private display: string;
    private email: string;
    private nick: string;
    private id: string;

    constructor (id:string,nick:string,email:string,display:string,location:string,brithdate:string) {
        this.id = id;
        this.nick = nick;
        this.email = email;
        this.display = display;
        this.location = location;
        this.birthdate = brithdate;
    }

    get _id(): string { return this.id; }

    get _nick(): string { return this.nick; }
    set _nick(nick: string) { this.nick = nick; }

    get _email(): string { return this.email; }
    set _email(email: string) { this.email = email; }

    get _display(): string { return this.display; }
    set _display(display: string) { this.display = display; }

    get _location(): string { return this.location; }
    set _location(location: string) { this.location = location; }

    get _birthdate() { return this.birthdate; }
    set _birthdate(birthdate) { this.birthdate = birthdate; }

}