import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { EnvironmentService, AuthenticationService } from 'src/app/core/services';
import { CookiesService } from 'src/app/core/services/cookies/cookies.service';
import { Identity } from 'src/app/shared/models/Identity';

@Injectable()
export class UsersService {
    
    constructor(
        public http: HttpClient,
        public envService: EnvironmentService,
        public cookies: CookiesService,
        public authentication: AuthenticationService
    ){}

    public getUsers(): any{
        var credentials = this.authentication._authinfo.get;
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/user/by-instance/'+ window['instance_id']).subscribe(resolve, reject);
        });  
    }

    
    public async getUser(userId: string) {
        var credentials = this.authentication._authinfo.get;
        let birthday;
        let location;
        let display;
        let email;
        let nick; 

        await this.getUserBirthday(userId).then((data)=>{ birthday = data }, error => {
            console.log("unable to load user birthday");    
        })

        await this.getUserNick(userId).then((data)=>{ nick = data },error => {
            console.log("unable to load user nick");    
        })

        await this.getUserEmail(userId).then((data)=>{ email = data },error => {
            console.log("unable to load user email");    
        })

        await this.getUserDisplay(userId).then((data)=>{ display = data },error => {
            console.log("unable to load user display");    
        })

        await this.getUserLocation(userId).then((data)=>{ location = data },error => {
            console.log("unable to load user location");    
        })

        return { nick,display,email,location,birthday }    
        
    }

    getUserBirthday(userid): any{
        return new Promise((resolve,reject) => {
           this.http.get(this.envService.read('apiUrl') +'/'+ userid + '/birthday').subscribe(resolve, reject);
        });  
    }

    getUserNick(userid): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/'+ userid + '/nick').subscribe(resolve, reject);
        });  
    }

    getUserDisplay(userid): any{
        return new Promise((resolve,reject) => {
           this.http.get(this.envService.read('apiUrl') +'/'+ userid + '/display').subscribe(resolve, reject);
        });  
    }

    getUserEmail(userid): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/'+ userid + '/email').subscribe(resolve, reject);
        });  
    }
    
    getUserLocation(userid): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/'+ userid + '/location').subscribe(resolve, reject);
        });  
    }

    async editUser(data,userId){
        let authinfo = this.authentication._authinfo.get();
        const options = {
            headers: new HttpHeaders({'Content-Type': 'application/json'}),
            body: authinfo
        };
        let nick = await new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl') + '/user/' + userId + '/nick',options).subscribe(resolve, reject);
        });  
        let display = await new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl') + '/user/' + userId + '/display',options).subscribe(resolve, reject);
        });  
        let email = await new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl') + '/user/' + userId + '/email',options).subscribe(resolve, reject);
        });  
        return { nick,display,email};
    }

    removeUser(userid): any{
        let authinfo = this.authentication._authinfo.get();
        const options = {
            headers: new HttpHeaders({'Content-Type': 'application/json'}),
            body: authinfo
        };
        return new Promise((resolve,reject) => {
            this.http.delete(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/user/' + userid,options).subscribe(resolve, reject);
        });  
    }

    createNewUser(userData):any {
        let authinfo = this.authentication._authinfo.get();
        const options = {
            headers: new HttpHeaders({'Content-Type': 'application/json'}),
            body: userData
        };
        return new Promise((resolve,reject) => {
            this.http.post(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/register',userData).subscribe(resolve, reject);
        }); 
    }

    setUserToRedactorPermisions(userId):any {
        return new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl') +'/'+ userId + '/perms',userId).subscribe(resolve, reject);
        });  
    }

}


