import { Routes } from "@angular/router";
import { UsersComponent } from "./users.component";
import { UsersCreateComponent } from "./create/users.create.component";
import { UsersEditComponent } from "./edit/users.edit.component";

/**
 *
 *
 *
 */


// Actual moudule routes
export const users_routes: Routes = [
    {
        path: 'users',
        loadChildren: 'src/app/common/modules/users/users.module#UsersModule'
    }
]

// Required for lazy-loading
export const routes: Routes = [
    {
        path: '',
        component: UsersComponent
    },
    {
        path: 'create',
        component: UsersCreateComponent
    },
    {
        path: 'edit/:userId',
        component: UsersEditComponent
    },
]
