import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { AuthenticationService } from "src/app/core/services";
import { UsersService } from "../users.service";


@Component({ 
    templateUrl: './users.edit.template.html',
    styleUrls: ['./users.edit.styles.scss']
})
export class UsersEditComponent implements OnInit {
    		
	public userForm: FormGroup;
	public submitted: boolean;
	public userId;
	data;
	loaded:boolean;

    constructor(
		private authentication: AuthenticationService,
		private userService: UsersService,
		private formBuilder: FormBuilder,
		private toast: ToastrService,
		private route: ActivatedRoute,
		private router: Router, 

    ){}

    
    ngOnInit(){
		this.route.paramMap.subscribe(params => { this.userId = params.get("userId") });
		this.userService.getUser(this.userId).then((data) => {
			//this.data = data;
			this.userForm = this.formBuilder.group({
				nick: [data.nick, [Validators.required]],
				display: [data.display, [Validators.required]],
				email: [data.email, [Validators.required]],
				pass: ['', [Validators.required]]   
			});
			this.loaded = true;
		})
		
        //this.loaded = true;

    }


	create() {
		console.log(this.userForm);
		this.submitted = true;
		if (!this.userForm.valid) { 
			//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
			return;
		}

	
		let data = {
			nick: this.userForm.get("nick").value,
			display: this.userForm.get("display").value,
			email: this.userForm.get("email").value,
			pass: this.userForm.get("pass").value
        }

		this.userService.editUser(data,this.userId).then((data) => {
				console.log(data);
				//TODO get id and set perms 
				// this.userService.setUserToRedactorPermisions(data).then((data) => {
				this.router.navigate(['redactor/users'])
				// })
			}, (error) => {
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}	
		);
	}


}
