import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthenticationService } from "src/app/core/services";
import { UsersService } from "../users.service";


@Component({ 
    templateUrl: './users.create.template.html',
    styleUrls: ['./users.create.styles.scss']
})
export class UsersCreateComponent implements OnInit {
    		
	public userForm: FormGroup;
	public submitted: boolean;
	loaded:boolean;

    constructor(
		private authentication: AuthenticationService,
		private userService: UsersService,
		private formBuilder: FormBuilder,
		private toast: ToastrService,
	 	private router: Router, 
    ){}

    
    ngOnInit(){
        this.loaded = true;
        this.userForm = this.formBuilder.group({
			nick: ['', [Validators.required]],
			display: ['', [Validators.required]],
			email: ['', [Validators.required]],
			pass: ['', [Validators.required]]   
        });
    }


	create() {
		console.log(this.userForm);
		this.submitted = true;
		if (!this.userForm.valid) { 
			//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
			return;
		}

	
		let data = {
			nick: this.userForm.get("nick").value,
			display: this.userForm.get("display").value,
			email: this.userForm.get("email").value,
			pass: this.userForm.get("pass").value
        }

		this.userService.createNewUser(data).then((data) => {
				console.log(data);
				//TODO get id and set perms 
				// this.userService.setUserToRedactorPermisions(data).then((data) => {
				this.router.navigate(['redactor/users'])
				// })
			}, (error) => {
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}	
		);
	}


}
